@use '../utils/shared' as *;

.cd-handle {
	position: absolute;
	height: 44px;
	width: 44px;
	left: 50%;
	top: 50%;
	margin-left: -22px;
	margin-top: -22px;
	border-radius: 50%;
	background: $primary-color url("../../assets/svg/comparison-slider-arrows.svg") no-repeat center center;
	cursor: pointer;
	box-shadow: 0 0 0 6px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 0.6), inset 0 1px 0 rgba(255, 255, 255, 0.3);
	opacity: 0;
	transform: translate3d(0, 0, 0) scale(0);
  .draggable {
    background-color: $primary-color-dark;
  }
}
.is-visible {
  .cd-handle {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
    transition: transform 0.3s 0.7s, opacity 0s 0.7s;
  }
}
